:root .header__img_fon {
  display: block;
  background-image: url(../img/video__img.jpeg);
  width: 100%;
  min-height: 100%;
}

:root .pre {
  display: none;
}

:root .mobile-menu {
  width: 100%;
  float: none;
}

:root .about-block {
  margin-left: -5px;
  margin-right: -5px;
}

:root .about-block::after {
  display: block;
  content: "";
  clear: both;
}

:root .about-block__item {
  float: left;
}

:root #container {
  width: 100%;
}

:root .portfolio-list {
  width: 100%;
  margin-left: -5px;
  margin-right: -5px;
}

:root .portfolio-list::after {
  display: block;
  content: "";
  clear: both;
}

:root .portfolio-list li {
  float: left;
}

:root .stages-items {
  margin-left: -5px;
  margin-right: -5px;
}

:root .stages-items::after {
  display: block;
  content: "";
  clear: both;
}

:root .stages-items__item {
  float: left;
}

.portfolio-filter {
  width: 100%;
}

.portfolio-filter li {
  font-size: 16px;
  margin-left: 2%;
}

:root .maps {
  display: none;
}

:root .slider-user {
  position: relative;
}

:root .user-box__img img {
  width: 100%;
}

:root .slick-dots {
  min-height: 70px;
  position: relative;
  left: 50%;
  margin-left: -140px;
  padding: 20px 0;
}

:root .slick-dots li {
  display: inline-block;
  text-align: center;
}

:root .maps-IE9 {
  width: 100%;
  min-height: 400px;
  background: url(../img/maps.png) no-repeat center;
  background-size: cover;
}

:root .footer-social {
  width: 150px;
}

:root .footer-social ul {
  width: auto;
}

:root .footer-social ul li a img {
  width: 24px;
}

@media screen and (max-width: 992px) {
  :root .top-menu__list li {
    width: 100%;
    margin-left: 0;
    display: block;
  }

  :root .about-block__item {
    margin-left: calc(25% + 5px);
  }

  :root .about-block__item .about-block__item:nth-child(odd) {
    clear: both;
  }

  :root .stages-items__item {
    margin-left: calc(25% + 5px);
  }
}

@media screen and (max-width: 767px) {
  :root .about-block__item {
    margin-left: calc(16.666666666666668% + 5px);
  }

  :root .stages-items__item {
    margin-left: calc(16.666666666666668% + 5px);
  }
}

@media screen and (max-width: 576px) {
  :root .about-block__item {
    margin-left: calc(0% + 5px);
  }

  :root .stages-items__item {
    margin-left: calc(0% + 5px);
  }
}

@media screen and (max-width: 380px) {
  .portfolio-filter {
    height: 70px;
  }
}