@import "smart-grid.less";

:root .header__img_fon {
	display: block;
	background-image: url(../img/video__img.jpeg);
	width: 100%;
	min-height: 100%;
}
:root .pre{
  display: none;
}

:root .mobile-menu {
  width: 100%;
  float: none;
}
:root .top-menu__list li{
  .md-block({
    width: 100%;
    margin-left: 0;
    display: block;
  });
}

:root .about-block{
  .row-float();
}

:root .about-block__item{
  .col-float();
  .shift-md-left(3);
  .shift-sm-left(2);
  .shift-xs-left(0);
  .md-block({
    .about-block__item:nth-child(odd){
      clear: both;
    }
  });
}

:root #container{
  width: 100%;
}

:root .portfolio-list{
  width: 100%;
  .row-float();
}
:root .portfolio-list li{
  .col-float();
}

:root .stages-items{
  .row-float();
}
:root .stages-items__item{
  .col-float();
  .shift-md-left(3);
  .shift-sm-left(2);
  .shift-xs-left(0);
}

.portfolio-filter{
  width: 100%;
  .xxs-block({ height: 70px;
  });
  li{
    font-size: 16px;
    margin-left: 2%;
  }
}

:root .maps{
  display: none;
}

:root .slider-user{
  position: relative;
}

:root .user-box__img{
  img{
    width: 100%;
  }
}

:root .slick-dots{
  min-height: 70px;
  position: relative;
  left: 50%;
  margin-left: -140px;
  padding: 20px 0;
  li{
    display: inline-block;
    text-align: center;
  }
}

:root .maps-IE9{
  width: 100%;
  min-height: 400px;
  background: url(../img/maps.png) no-repeat center;
  background-size: cover;
}

:root .footer-social{
  width: 150px;
  ul{
    width: auto;
    li a img {
      width: 24px;
    }
  }
}





